import React from "react";
import { Layout } from "antd";
import { inject, observer } from 'mobx-react/index'
import LoadableComponent from "../../../utils/LoadableComponent";
import { BrowserRouter as Router,Route,Switch,Redirect} from "react-router-dom";
// import Container from "./Container";
// import Slider from "../Slider/Slider";
import Edit from "../Edit/Edit";

import './index.css'
const Slider = LoadableComponent(() => import('../Slider/Slider'))
const Container = LoadableComponent(() => import('./Container'))

const { Header, Content, Footer, Sider } = Layout;

@inject('headerStore') @observer

class Index extends React.Component {

    constructor(props) {
        super(props)
        console.log(this.props)
        this.props.headerStore.setCurrunt('/Video/Index')
    }
    render() {

        return (
            <div id="Video">
                <Switch>
                    <Route   path='/Video/Index' component={Container}></Route>
                    <Route    path='/Video/Edit' component={Edit}></Route>
                </Switch>
            </div>
        )
    }
}

export default Index