import React from "react";
import { Collapse, message, Icon, Form, Input } from 'antd'
import api from "../../../api/api";
import { Player, BigPlayButton } from 'video-react';
import 'video-react/dist/video-react.css';
import { isAuthenticated } from "../../../utils/Session";
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import './index.css'
// import { clearInterval } from "timers";
const Panel = Collapse.Panel;
const getSvgIcon = (style = {}, ) => {

    const path = 'M715.8 493.5L335 165.1c-14.2-12.2-35-1.2-35 18.5v656.8c0 19.7 20.8 30.7 35 18.5l380.8-328.4c10.9-9.4 10.9-27.6 0-37z';
    return (
        <span style={style}>
            <i >
                <svg
                    viewBox="0 0 1024 1024"
                    width="1em"
                    height="1em"
                    fill="currentColor"
                    style={{ verticalAlign: '-.125em' }}
                >
                    <path d={path} />
                </svg>
            </i>

        </span>
    );
};

function expandicon(props) {
    return getSvgIcon({
        position: 'absolute',
        right: 'auto',
        left: '0px',
        color: '#B4B4B4',
        transform: `rotate(${props.isActive ? 90 : 0}deg)`,
        top: 0
    }, props);
}

class Index extends React.Component {
    constructor(props) {
        super(props)

    }
    state = {
        backUserId: '',
        value: '',
        backUserId: 1,
        deptId: 1,
        listCategory: [],
        secondId: 0,
        selectId:0,
        firstId: 0,
        videoUrl: 'https://dev-img.oss-cn-shanghai.aliyuncs.com/6dDNGpYanaAhBfimWhuN.mp4',
        videoUrl1: '',
        isShow: true,
        firstImg: '',
        videoLength: '',
        title: '',
        shieldList: []

    }
    componentWillMount() {
        let userInfo = JSON.parse(isAuthenticated().split('?')[0])
        this.setState({
            backUserId: userInfo.backUserId, deptId: userInfo.deptId,
        })
        if (this.props.location.params && this.props.location.params.id) {
            api('getArticleInfo', 'post', {}, {
                'articleId': this.props.location.params.id
            }).then(res => {
                if (!res.data.errorMessage) {
                    console.log(res)

                    this.props.form.setFieldsValue({
                        name: res.data.data.content,
                        title: res.data.data.title
                    });
                    this.setState({
                        videoUrl: res.data.data.videoUrl,
                        videoUrl1: res.data.data.videoUrl,
                        secondId: res.data.data.secondCateId ? res.data.data.secondCateId : 0,
                        firstId: res.data.data.firstCateId,
                        value: res.data.data.content,
                        title: res.data.data.title,
                        isShow: false,
                        videoLength: res.data.videoLength,
                        firstImg: res.data.data.firstImg,
                        selectId:res.data.data.firstCateId
                    })
                } else {
                    message.warning(res.data.errorMessage, 2, () => {
                        this.props.history.push('/Video/Index')
                        // this.props.history.go(-1)
                    })

                }
            })
        }
        api('listCategory', 'post', {}, { 'type': 2, status: 1 }).then(res => {
            if (!res.data.errorMessage) {
                let array = res.data.data
                for (let index = 0; index < array.length; index++) {
                    const element = array[index];
                    element.isCheck = false
                    for (let index = 0; index < element.list.length; index++) {
                        const ele = element.list[index];
                        ele.isCheck = false
                    }
                }
                this.setState({
                    listCategory: array,

                })

            } else {
                message.warning(res.data.errorMessage)
            }
        })
        this.getShieldList().then(res => {
            if (res.data.success) {
                this.setState({
                    shieldList: res.data.list
                })
            }
        })
    }
    componentDidMount() {
        NProgress.done()
    }
    componentWillUnmount() {
        NProgress.start()
    }
    getShieldList = async (pageNo = 1, pageSize = 1000000) => {
        let res = api('getShieldList', 'post', { pageNo, pageSize })
        return res
    }
    onChange = (e) => {
        // console.log(this.props.form.getFieldsValue(['name']).title)

        this.setState({
            title: e.target.value
        })


    }
    expandIcon = () => {
        return (
            <span>
                <Icon type="caret-right" />
                <Icon type="caret-left" />
            </span>
        )
    }
    onCollapseChange = (file) => {
        console.log(file)
this.setState({
    selectId:file
})

    }
    onChangeSecond = (val, val1) => {
        console.log(val, val1)
        let array = this.state.listCategory;
        for (let index = 0; index < array.length; index++) {
            const element = array[index];
            if (val == index) {

            }
            for (let i = 0; i < element.list.length; i++) {
                const ele = element.list[i];

                if (val == index && val1 == i) {
                    ele.isCheck = ele.isCheck ? false : true
                    element.isCheck = ele.isCheck ? false : true

                    this.setState({
                        secondId: ele.isCheck ? ele.id : 0,
                        firstId: ele.isCheck ? element.id : 0
                    })
                } else {
                    ele.isCheck = false
                }
            }
        }

        this.setState({
            listCategory: array,

        })
    }
    onChangeFirst = (val, e) => {
        console.log(val, e)
        e.preventDefault()
        let array = this.state.listCategory;
        for (let index = 0; index < array.length; index++) {
            const element = array[index];
            if (val == index) {
                element.isCheck = element.isCheck ? false : true
                this.setState({
                    firstId: element.isCheck ? element.id : 0
                })
            } else {
                element.isCheck = false
            }
            for (let i = 0; i < element.list.length; i++) {
                const ele = element.list[i];

                ele.isCheck = false
                this.setState({
                    secondId: 0
                })
            }
        }
        this.setState({
            listCategory: array,
            firstId: this.state.listCategory[val].id,
            selectId:this.state.listCategory[val].id
        })
    }
    getVideo = (e) => {
        console.log(e.target.files)
        NProgress.start()
        var clearTime
        const formData = new FormData();
        formData.append('file', e.target.files[0]);

        api('uploadImg', 'post', formData, { 'resType': 2 }).then(res => {
            console.log(res)
            if (res.data.success) {



                clearTime = setInterval(() => {
                    const { player } = this.refs.player.getState();
                    console.log(player.readyState)
                    if (player.readyState) {
                        clearInterval(clearTime)
                        NProgress.done()
                        message.success('上传成功！')
                        this.setState({
                            videoUrl: res.data.ossAddress,

                            videoUrl1: res.data.ossAddress
                        })
                        // this.refs.player.load();
                        // this.refs.player.addEventListener("loadstart", () => {
                        this.setState({
                            isShow: false,
                        })
                        // }
                        // );

                    }
                }, 1000)
            } else {
                message(res.data.errorMessage)
            }
        })
        api('getVideoLength', 'post', formData).then(res => {
            console.log(res)
            if (res.data.success) {
                this.setState({
                    videoLength: res.data.data
                })
            } else {
                message(res.data.errorMessage)
            }

        })
        api('getFirstImg', 'post', formData).then(res => {
            console.log(res)
            if (res.data.success) {
                this.setState({
                    firstImg: res.data.data
                })
            } else {
                message(res.data.errorMessage)
            }
        })
    }
    handleSubmit = () => {
        let value = this.props.form.getFieldsValue(['synopsis', 'title'])

        if (value.title) {
            
                if (value.synopsis) {
                    api('checkKeyword', 'post', {}, {
                        'content': encodeURI(value.title+','+value.synopsis),
        
                    }).then(res => {
                        if (res.data.data.length==0) {
                            if (this.state.videoUrl1) {
                                if (this.props.location.params && this.props.location.params.id) {
                                    api('editArticle', 'post', {}, {
                                        'articleId': this.props.location.params.id,
                                        'adminId': this.state.backUserId,
                                        'title': value.title,
                                        'content': value.synopsis,
                                        "firstImg": this.state.firstImg,
                                        "videoLength": this.state.videoLength,
                                        "video_url": this.state.videoUrl1,
                                        'secCategoryId': this.state.secondId != 0 ? this.state.secondId : '',
                                        'categoryId': this.state.secondId != 0 ? this.state.secondId : this.state.firstId,
                                        'firstCategoryId': this.state.firstId,
                                    }).then(res => {
                                        if (!res.data.errorMessage) {
                                            console.log(res)
                                    message.success('修改成功!')

                                            this.props.history.go(-1)
                                        } else {
                                            message.warning(res.data.errorMessage, 2, () => {
                                                this.props.history.push('/Video/Index')
                                                // this.props.history.go(-1)
                                            })
                                        }
                                    })
                                } else {
                                    if (this.state.firstId != 0 || this.state.secondId != 0) {
                                        api('addArticle', 'post', {
                                            "backUserId": this.state.backUserId,
                                            "content": value.synopsis,
                                            "firstImg": this.state.firstImg,
                                            "status": "1",
                                            "title": value.title,
                                            "type": 2,
                                            "videoLength": this.state.videoLength,
                                            "video_url": this.state.videoUrl1,
                                            'deptId': this.state.deptId,
                                            'secCategoryId': this.state.secondId != 0 ? this.state.secondId : '',
                                            'categoryId': this.state.secondId != 0 ? this.state.secondId : this.state.firstId,
                                            'firstCategoryId': this.state.firstId,
        
                                        }).then(res => {
                                            if (!res.data.errorMessage) {
                                                console.log(res)
                                    message.success('添加成功，等待审核中...')
                                    this.props.history.push('/Video/Index')
                                                // this.props.history.go(-1)
                                            } else {
                                                message.warning(res.data.errorMessage, 2, () => {
                                                    this.props.history.push('/Video/Index')
                                                    // this.props.history.go(-1)
                                                })
                                            }
                                        })
                                    } else {
                                        message.warning('请选择类目！')
        
                                    }
                                }
        
                            } else {
                                message.warning('请上传视频！')
                            }
                        } else {
                            // message.warning(res.data.errorMessage)
                            message.warning(`${res.data.data.join(',')}是关键字，请删除对应中的关键词`)
                        }
                    })
                   
                } else {
                    message.warning('请输入文章简介!')
                }
            

        } else {
            message.warning('请输入文章标题!')
        }

    }
    delete = () => {
        this.refs.file.value = ''
        this.setState({
            isShow: true,
            videoUrl1: '',
            firstImg: '',
            videoLength: ''
        })
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <div id="editVideo">
                <div className="content">
                    <div className="videoContent">
                        <div className="videoBox">
                            <div className={this.state.isShow ? 'addVideo' : 'addVideo hide'} >
                                <div className="text">
                                    <img src={require('../../../assets/images/img-addVideo.png')} alt="" />
                                    <span>上传视频</span>
                                </div>
                                <input type="file" accept='video/*' ref='file' onChange={this.getVideo} />
                            </div>
                            <div className="video">
                                <Player ref="player" fluid={false} src={this.state.videoUrl} width={'100%'} height={331}>
                                    <BigPlayButton position="center" />

                                </Player>
                                <span className='delete' onClick={this.delete}>
                                    <i >
                                        <svg
                                            viewBox="0 0 1024 1024"
                                            width="1em"
                                            height="1em"
                                            fill="currentColor"
                                            style={{ verticalAlign: '-.125em' }}
                                        >
                                            <path d='M677.647059 256l0-90.352941c0-37.436235-23.461647-60.235294-61.771294-60.235294L408.094118 105.411765c-38.249412 0-61.741176 22.799059-61.741176 60.235294l0 90.352941-180.705882 0 0 60.235294 60.235294 0 0 512c0 54.272 33.972706 90.352941 90.352941 90.352941l391.529412 0c55.085176 0 90.352941-33.490824 90.352941-90.352941l0-512 60.235294 0 0-60.235294L677.647059 256zM406.588235 165.647059l210.823529 0-1.264941 90.352941L406.588235 256 406.588235 165.647059zM737.882353 858.352941l-451.764706 0 0-542.117647 451.764706 0L737.882353 858.352941zM466.823529 376.470588l-58.729412 0-1.505882 391.529412 60.235294 0L466.823529 376.470588zM617.411765 376.470588l-60.235294 0 0 391.529412 60.235294 0L617.411765 376.470588z' />
                                        </svg>
                                    </i>

                                </span>
                                {/* <video ref="player" src={this.state.videoUrl} controls></video> */}
                            </div>
                        </div>

                    </div>
                    <div className="title">简介</div>

                    <div className="textArea synopsis">
                        <Form >
                            <Form.Item label=''>
                                {getFieldDecorator('synopsis', {
                                    initialValue: this.state.value
                                })(<Input
                                    placeholder="请输入文章简介"
                                    autocomplete="off"


                                />)}
                            </Form.Item>

                        </Form>
                    </div>
                    <div className="title">标题</div>

                    <div className="textArea ">
                        <Form >
                            <Form.Item label=''>
                                {getFieldDecorator('title', {
                                    initialValue: this.state.title
                                })(<Input
                                    placeholder="请输入文章标题"
                                    autocomplete="off"
                                    onChange={this.onChange}
                                    maxLength={50}
                                />)}
                            </Form.Item>
                            <div className="textLength">{this.state.title.length}/50</div>
                        </Form>
                    </div>
                    <div className="title">选择类目</div>
                    <div className="columnList">
                        <Collapse accordion='true' defaultActiveKey={this.state.selectId ? this.state.selectId.toString() : '0'} activeKey={[this.state.selectId ? this.state.selectId.toString() : '0']} expandIcon={expandicon} bordered={false} showArrow={false} onChange={this.onCollapseChange}>
                            {
                                this.state.listCategory.map((res, index) => {
                                    if (res.list.length > 0) {
                                        return (
                                            <Panel header={<span className='first' key={res.id}>{res.name}</span>} key={res.id} >
                                                <div >
                                                    {res.list && res.list.map((res, i) => {
                                                        if (res && res.name) {
                                                            return (<span className='second' onClick={() => {
                                                                this.onChangeSecond(index, i)
                                                            }} key={res.id}>{
                                                                    res.id == this.state.secondId ? <Icon type="check-circle" theme="filled" /> : <span className='icon'></span>
                                                                }{res.name}</span>)
                                                        }


                                                    })}
                                                </div>
                                            </Panel>
                                        )
                                    } else {
                                        if (res.id != 1) {
                                            return (<div className='firstColumn' key={res.id}>
                                                <span className='second' onClick={(e) => {
                                                    this.onChangeFirst(index, e)
                                                }}>{
                                                        res.id == this.state.firstId ? <Icon type="check-circle" theme="filled" /> : <span className='icon'></span>
                                                    }{res.name}</span>
                                            </div>)
                                        }
                                    }

                                })
                            }

                        </Collapse>
                    </div>
                    <div className="btn">
                        <span className='submit' onClick={this.handleSubmit}>发布</span>
                        <span className='cancel' onClick={this.setCancel}>取消</span>
                    </div>
                </div>
            </div>
        )
    }
}
const WrappedRegistrationForm = Form.create()(Index);
export default WrappedRegistrationForm